/** Store Style **/ 



.main {
    font-family: 'Source Sans Pro', sans-serif;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px; 
}

.link{
    text-decoration: none;
    color: black;
}

.link:hover{
  
    color: black;
}
